/*------------------------------------*\
  #Box-sizing
\*------------------------------------*/
html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

/*------------------------------------*\
  #Document
\*------------------------------------*/
html {
  font-size: 0.91481vw; }
  @media (max-width: 87.5em) {
    html {
      font-size: 13px; } }

/*------------------------------------*\
  #Body
\*------------------------------------*/
body {
  font-family: "Open Sans", sans-serif;
  background-color: #F2F2F2; }

/*------------------------------------*\
  #Headings
\*------------------------------------*/
h1, h2, h3, h4, h5, h6 {
  margin: 0; }

/*------------------------------------*\
  #Buttons
\*------------------------------------*/
.btn {
  width: 18.0625rem;
  height: 3.0625rem;
  font-size: 0.875rem;
  font-weight: bold;
  background-image: linear-gradient(90deg, #946DFF, #2AACEE);
  color: #FFF;
  cursor: pointer;
  border: none;
  border-radius: 0.3125rem; }

.btn--special {
  background-image: none;
  background-color: #FFF;
  color: #757575; }

/*------------------------------------*\
  #Box-group
\*------------------------------------*/
.box-group {
  display: flex;
  justify-content: center;
  margin-top: 5.75rem; }
  @media (max-width: 56.25em) {
    .box-group {
      flex-direction: column;
      align-items: center;
      margin-top: 0; } }

/*------------------------------------*\
  #Box
\*------------------------------------*/
.box {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 21.5625rem;
  background-color: #FFF;
  padding-top: 1.6875rem;
  padding-bottom: 3.0625rem;
  color: #767676;
  font-size: 0.8125rem; }

.box__header {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 0.9375rem; }

.box--special {
  margin-top: -1.8125rem;
  margin-bottom: -1.8125rem;
  padding-top: 3.5rem;
  background-image: linear-gradient(180deg, #946DFF, #2AACEE);
  color: #FFF; }

/*------------------------------------*\
  #Price-box
\*------------------------------------*/
.price-box {
  width: 8.9375rem;
  height: 8.9375rem;
  border-radius: 50%;
  background-image: linear-gradient(180deg, #946DFF, #2AACEE);
  font-size: 1.125rem;
  margin-bottom: 1.5625rem;
  color: #FFF;
  text-align: center; }

.price-box__price {
  font-size: 1.25rem; }

.price-box__larger {
  font-size: 2.8125rem; }

.price-box--special {
  background-image: none;
  background-color: #FFF;
  color: #757575; }

/*------------------------------------*\
  #Icons
\*------------------------------------*/
@font-face {
  font-family: 'fontello';
  src: url(data:application/vnd.ms-fontobject;base64,);
  src: url(data:application/vnd.ms-fontobject;base64,#iefix) format("embedded-opentype"), url(data:font/woff2;base64,) format("woff2"), url(data:font/woff;base64,) format("woff"), url(data:font/ttf;base64,) format("truetype"), url(img/fontello.svg#fontello) format("svg");
  font-weight: normal;
  font-style: normal; }

/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
/*
@media screen and (-webkit-min-device-pixel-ratio:0) {
  @font-face {
    font-family: 'fontello';
    src: url('../font/fontello.svg?24957063#fontello') format('svg');
  }
}
*/
[class^="icon-"]:before, [class*=" icon-"]:before {
  font-family: "fontello";
  font-style: normal;
  font-weight: normal;
  speak: none;
  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: .2em;
  text-align: center;
  /* opacity: .8; */
  /* For safety - reset parent styles, that can break glyph codes*/
  font-variant: normal;
  text-transform: none;
  /* fix buttons height, for twitter bootstrap */
  line-height: 1em;
  /* Animation center compensation - margins should be symmetric */
  /* remove if not needed */
  margin-left: .2em;
  /* you can be more comfortable with increased icons size */
  /* font-size: 120%; */
  /* Font smoothing. That was taken from TWBS */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* Uncomment for 3D effect */
  /* text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3); */ }

.icon-db:before {
  content: '\e801'; }

/* '' */
.icon-email:before {
  content: '\e802'; }

/* '' */
.icon-monitor:before {
  content: '\e803'; }

/* '' */
